import string from './css.js'
// å¯¼å…¥ æ¨¡å—åŒ–

const player = {
    id: undefined,
    time: 100,
    n: 1,
    ui: {
        demo: document.querySelector('#demo'),
        demo2: document.querySelector('#demo2')
    },
    //åˆå§‹åŒ–å†…å®¹
    init: () => {
        player.ui.demo.innerText = string.substr(0, player.n)
        player.ui.demo2.innerHTML = string.substr(0, player.n)
        player.bindEvents()
        player.play()

    },
    events: {
        '#btnPause':'pause',
        '#btnPlay':'play',
        '#btnNormal':'Normal',
        '#btnSlow':'Slow',
        '#btnQuick':'Quick'
    },
    bindEvents:()=>{
        for(let key in player.events){
            if(player.events.hasOwnProperty(key)){
                // çœ‹play.eventsçš„ç”±åŽŸåž‹ä¸­æ˜¯å¦æœ‰key
                const value = player.events[key] //éåŽ†eventsäº‹ä»¶å†…çš„å€¼
                document.querySelector(key).onclick = player[value] //ç›´æŽ¥è°ƒç”¨playerçš„æ–¹æ³•
            }
        }
    },
    run: () => {
        player.n += 1
        if (string.length < player.n) {
            window.clearInterval(player.id)
            //   å–æ¶ˆè®¡æ—¶å™¨
            return
        }
        console.log(player.n + ':' + string.substr(0, player.n))
        player.ui.demo.innerText = string.substr(0, player.n)
        player.ui.demo2.innerHTML = string.substr(0, player.n)
        player.ui.demo.scrollTop = player.ui.demo.scrollHeight
//    å°†æ»šåŠ¨æ¡æ‹‰åˆ°åº• æœ‰é—®é¢˜scrollHeightè¦å‡åŽ»æ»šåŠ¨æ¡çš„é«˜åº¦
    },
    play: () => {
        window.clearInterval(player.id)
        // è§£å†³å¤šæ¬¡ç‚¹å‡»æ’­æ”¾åŽï¼Œæ— æ³•æš‚åœçš„é—®é¢˜
        // å› ä¸ºç‚¹å‡»å¤šæ¬¡åŽ è®¾ç½®äº†ä¸€ä¸ªæ–°çš„å®šæ—¶å™¨ï¼Œæ— æ³•æš‚åœæ—§çš„å®šæ—¶å™¨äº†
        player.id = setInterval(player.run, player.time)
    },
    pause: () => {
        window.clearInterval(player.id)
    },
    Normal: () => {
        player.pause()
        player.time = 100
        player.play()
    },
    Slow: () => {
        player.pause()
        player.time = 200
        player.play()
    },
    Quick: () => {
        player.pause()
        player.time = 0
        player.play()
    }
}

player.init()

